import React, {useState} from 'react';
import './Posts.css';
import {useSelector} from "react-redux";

const Posts = (props) => {


    const postsList = props.post.map((item) => {
        return(
            <div className='post__div'>
                <h3 className='post__h3' >{item.type}:</h3>
                <p className='post__p' ><strong></strong>: {item.sum}</p>
                <p className='post__p' ><strong></strong>: {item.category}</p>
            </div>
        )
    });

    return (
        <>
            {postsList.length === 0 ? (<div className='error'>
                <div className="error a01"></div>
                <div className="error a02"></div>
                <div className="error a03"></div>
                <div className="error a04"></div>
                <div className="error a05"></div>
                <div className="error a06">!!!</div>
            </div>) : postsList}
        </>
    );
};

export default Posts;

