import React, {useState} from 'react';
import './MyProjects.css';
import PopupWithForm from "../PopupWithForm/PopupWithForm";
import ImagePopup from "../ImagePopup/ImagePopup";
import MyButton from "../../uiKit/MyButton/MyButton";

const MyProjects = (props) => {

    const [isPopupOpen, setPopupOpen] = useState(false);
    const [paragraphValue, setParagraphValue] = useState('');
    const [nameValue, setNameValue] = useState('');
    const [imageValue, setImageValue] = useState('');
    // const openPopUp = () => {
    //     setPopupOpen(true)
    //
    // }

    const openMesto = () => {
        setPopupOpen(true)
        setImageValue('https://avatars.githubusercontent.com/u/102199209?v=4');

        setParagraphValue('    ,   , ' +
            '          . ' +
            '     -,  : ' +
            'flex,     ,       ' +
            '   JavaScript,          (  Nested).');
        setNameValue('Mesto');
    }

    const openMestoReact = () => {
        setPopupOpen(true);
        setImageValue('https://avatars.githubusercontent.com/u/102199209?v=4');
        setParagraphValue('    ,   , ' +
            '          . ' +
            '     -,  :' +
            ' flex,     ,       ' +
            '   React,          (  Nested).');
    }

    const openTravel = () => {
        setPopupOpen(true);
        setImageValue('https://avatars.githubusercontent.com/u/102199209?v=4');
        setParagraphValue('     .\n' +
            '\n' +
            '     : -\n' +
            '\n' +
            '   .    ,      skew, shadow' +
            ',    .        input,      ')
    }

    const openHowLearn = () => {
        setPopupOpen(true);
        setImageValue('https://avatars.githubusercontent.com/u/102199209?v=4');
        setParagraphValue('Regular site\n' +
            '\n' +
            'It\'s just a one page website. Virtually non-interactive. There is a transition to a video on YouTube using an iframe\n' +
            '\n' +
            '   .    ,      skew, shadow, ' +
            '   .        input,      ')
    }

    const openTodoReact = () => {
        setPopupOpen(true);
        setImageValue('https://avatars.githubusercontent.com/u/102199209?v=4');
        setParagraphValue('ToDo List         .   ' +
            '      .     -   , ' +
            '    ,         . ')
    }

    return (
        <div>
            <h3> :</h3>
        <div className='projects'>
            <div className='projects__container'>
                <h5 className={'projects__title'}></h5>
                <button className={'projects__description'} onClick={(event) =>openMesto()} >...</button>
                <a href='https://andoreides.github.io/mesto/'>
                    <img className='projects__img' src={'https://avatars.githubusercontent.com/u/102199209?v=4'} />
                </a>
            </div>
            <div className='projects__container'>
                <h5 className={'projects__title'}> </h5>
                <button className={'projects__description'} onClick={(e)=>openMestoReact()}>...</button>
                <a href='https://andoreides.github.io/Portfolio-mesto/'>
                    <img className='projects__img' src={'https://avatars.githubusercontent.com/u/102199209?v=4'} />
                </a>
            </div>
            <div className='projects__container'>
                <h5 className={'projects__title'}>Travel</h5>
                <button className={'projects__description'} onClick={(e)=>openTravel()} >...</button>
                <a href='https://andoreides.github.io/russian-travel/'>
                    <img className='projects__img' src={'https://avatars.githubusercontent.com/u/102199209?v=4'} />
                </a>
            </div>
            <div className='projects__container'>
                <h5 className={'projects__title'}>How to learn</h5>
                <button className={'projects__description'} onClick={(event) =>openHowLearn() } >...</button>
                <a href='https://github.com/Andoreides/how-to-learn.git'>
                    <img className='projects__img' src={'https://avatars.githubusercontent.com/u/102199209?v=4'} />
                </a>
            </div>
            <div className='projects__container'>
                <h5 className={'projects__title'}>Balance React</h5>
                <button className={'projects__description'} onClick={(e) => openTodoReact()} >...</button>
                <a href='https://Andoreides.github.io/balance-project'>
                    <img className='projects__img' src={'https://avatars.githubusercontent.com/u/102199209?v=4'} />
                </a>
            </div>
        </div>
            <div className={'projects__div-offer'}><a href={'https://t.me/unknownhzhz'} className={'projects__offer'}>   :)</a></div>
            <ImagePopup
                inputName='projects'
                name={nameValue}
                isOpen={isPopupOpen}
                isClose={setPopupOpen}
                description={paragraphValue}
                link={imageValue}
                // submitText=''
                // onSubmit={createPost}
            >
                {/*<p >{paragraphValue}</p>*/}
                {/*<img src={imageValue} alt={nameValue} />*/}

            </ImagePopup>
        </div>
    );
};

export default MyProjects;