import React, {useState} from 'react';
import {MyInput} from "../../uiKit/MyInput/MyInput";
import MyButton from "../../uiKit/MyButton/MyButton";
import './UserData.css';

const UserData = ({setAuth, isAuth}) => {

    const [inputUserLogin, setInputUserLogin] = useState('');
    const [inputUserPassword, setInputUserPassword] = useState('');

    const handleClickUserBtn = () => {
        setInputUserLogin('')
        setInputUserPassword('')
        setAuth(!isAuth)
    }

    return (
         <div className='userdata__div'>
            <a href='https://github.com/Andoreides' className='userdata__a' >
                <img src='https://avatars.githubusercontent.com/u/102199209?v=4' className='userdata__img' alt='' />
            </a>
            <h3 className='userdata__h3'>   </h3>
            <MyInput placeholder='' onChange={setInputUserLogin} value={inputUserLogin} />
            <MyInput placeholder='' onChange={setInputUserPassword} value={inputUserPassword} />
            {/*<MyButton title='' style='MyButton' />*/}
            <button className='userdata__btn' onClick={handleClickUserBtn}></button>
             <p>      "".  -      </p>
        </div>
    );
};

export default UserData;