import React, {useState} from 'react';
import './NavBar.css';
import {Link, Redirect} from "react-router-dom";

const NavBar = ({isAuth}) => {



    const [isOpen, setIsOpen] = useState(false);
    if (!isAuth) {
        return <Redirect to="/userdata"/>;
    }
    // } else {
    //     return <Redirect to="/" />;
    // }
    const openNavbar = () => {
        setIsOpen(!isOpen);
    }

    return (
        <div>
        <div className="toggle">
            <button onClick={()=>openNavbar()} className='btn_menu'>Menu</button>
        </div>

        <div className={`menu ${isOpen && 'active' } `}>
            <ul>
                <li>
                    <button onClick={()=>openNavbar()} className='btn_menu '><Link to='/'></Link></button>
                </li>
                <li>
                    <button onClick={()=>openNavbar()} className='btn_menu navigate'><Link to='/posts'></Link></button>
                </li>
                <li>
                    <button onClick={()=>openNavbar()} className='btn_menu navigate'><Link to='/userdata'></Link></button>
                </li>
                <li>
                    <button onClick={()=>openNavbar()} className='btn_menu navigate'><Link to='/projects'></Link></button>
                </li>
            </ul>
        </div>
        </div>
    );
};

export default NavBar;